import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import "./MoneyGiven.css";
import HeadSubHead from "../RoutePagesHead_SubHead/HeadSubHead";
import RouteSearchSort from "../RoutePagesSearch_Sort/RouteSearchSort";
import AddTransactionModal from "../../AddTransactionModal/AddTransactionModal";
import UserAddIcon from "../../../images/user-plus.svg";
//import noTransactionEntryImage from "../../NoTransactionEntry";
import UserTransactionThumbnailsContainer from "../../UserTransactionThumbnailsContainer/UserTransactionThumbnailsContainer";
import { UserTransactionDataContext } from "../../../Contexts/UserTransactionDataContext";
import successImage from "../../../images/success.gif";
import ErrorSuccessInfoModal from "../../ErrorSuccessInfoModal/ErrorSuccessInfoModal";
import { AppThemeContext } from "../../../Contexts/AppThemeContext";
class MoneyGiven extends Component {
	static contextType = AppThemeContext;

	constructor(props) {
		super(props);
		this.state = {
			isModalContainerVisible: false,
		};
		this.toggleModalContainerVisibility = this.toggleModalContainerVisibility.bind(
			this
		);
	}
	toggleModalContainerVisibility(isVisible) {
		this.setState({
			isModalContainerVisible: isVisible,
		});
	}
	componentDidMount() {
		document.title = "Money Given - TODO ";
		window.addEventListener("scroll", () => {
			console.log(window.scrollY);
			if (window.scrollY > 200) {
				document
					.querySelector(".search-sort-add-item")
					.classList.add("search-sort-add-operation-container__fixed");
			} else {
				document
					.querySelector(".search-sort-add-item")
					.classList.remove("search-sort-add-operation-container__fixed");
			}
		});
	}
	render() {
		console.log(this.context);
		return (
			<div className="money-given-container">
				<div className="container">
					<HeadSubHead
						head="You Have Given Money to These People"
						subhead="Do not forget to take the money back."
					/>
					<div
						className="d-flex search-sort-add-item justify-content-center align-items-center"
						style={{
							background:
								this.context.selectedTheme == "light" ? "#fff" : "#1a1a1a",
						}}
					>
						<RouteSearchSort />
						<button
							onClick={() => this.toggleModalContainerVisibility(true)}
							className="d-flex justify-content-center font-weight-light align-items-center open-add-transaction-modal-btn"
						>
							+
							{/*
						<img
							src={UserAddIcon}
							alt="Add User Icon"
							className="img-fluid add-user-icon ml-2"
							/>*/}
						</button>
					</div>
					<UserTransactionThumbnailsContainer doesUserExist="false" />
				</div>
				<AddTransactionModal
					isModalContainerVisible={this.state.isModalContainerVisible}
					toggleModalContainerVisibility={this.toggleModalContainerVisibility}
				/>
				<ErrorSuccessInfoModal
					modalImage={successImage}
					modalText="User added successfully.!"
				/>
			</div>
		);
	}
}

export default withRouter(MoneyGiven);



